import { Injectable, OnInit } from '@angular/core';
import { Http, Headers, Response, URLSearchParams, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/toPromise';
import { Router } from '@angular/router';
import { NzNotificationService } from 'ng-zorro-antd';

@Injectable()
export class AuthClient implements OnInit {

    private static URL = {
        "approles": " /app/v1/userperm/approles",
        "resource": " /app/v1/userperm/resource"
    };

    appId: string = '1';

    public resources = {};

    constructor(private http: Http,
                private router: Router,
                private _notification: NzNotificationService) {
        let appPros = this.get(AuthClient.URL.resource + "?appIds=" + this.appId);
        appPros.then(result => {
            if (!result || !result.success) {
                this._notification.error("无权限", result.message || "您暂无访问权限！");
                return;
            }
            let authSet = {};
            this.findAllResources(result.body, authSet);
            this.resources = authSet;
            console.log(this.resources);
        }, err => {
            this._notification.error("错误", "系统繁忙请稍后重试！");
        })
    }

    ngOnInit() {
    }

    private findAllResources(resList: any[], authSet) {
        if (!resList) {
            return;
        }
        for (let res in resList) {
            if (resList[res]['resId']) {
                authSet[resList[res]['resId']] = true;
            }
            if (resList[res]['children']) {
                this.findAllResources(resList[res]['children'], authSet);
            }
        }
    }

    private get(url): Promise<any> {
        return this.http.get(url).toPromise()
            .then(res => res.json())
            .catch(error => Promise.reject(error.message || error));
    }

}



